import {
  Component,
  EventEmitter,
  forwardRef,
  Input,
  Output,
  ViewEncapsulation
} from '@angular/core';
import { AsyncPipe, NgClass, NgForOf, NgIf } from '@angular/common';
import {
  AbstractControl,
  FormsModule,
  NG_VALIDATORS,
  NG_VALUE_ACCESSOR,
  ValidationErrors,
  Validator
} from '@angular/forms';
import { MatFormFieldModule } from '@angular/material/form-field';
import { MatRadioChange, MatRadioModule } from '@angular/material/radio';
import { BooleanInput, coerceBooleanProperty } from '@angular/cdk/coercion';

import { BaseControlValueAccessor } from 'sidsc-components/core';

export type DscRadioButtonLabelPosition = 'before' | 'after';

export type DscRadioButtonVariant = 'highlight' | 'accent' | 'danger';

export type DscRadioButtonSize = 'large' | 'standard' | 'small';

@Component({
  selector: 'dsc-radio-button',
  standalone: true,
  imports: [MatRadioModule, FormsModule, NgForOf, MatFormFieldModule, NgIf, NgClass, AsyncPipe],
  encapsulation: ViewEncapsulation.None,
  templateUrl: './dsc-radio-button.component.html',
  styleUrls: ['./dsc-radio-button.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => DscRadioButtonComponent),
      multi: true
    },
    {
      provide: NG_VALIDATORS,
      useExisting: DscRadioButtonComponent,
      multi: true
    }
  ]
})
export class DscRadioButtonComponent extends BaseControlValueAccessor<any> implements Validator {
  @Input() name!: string;

  @Input() label?: string;

  @Input('aria-describedby') ariaDescribedby!: string;

  @Input('aria-label') ariaLabel!: string;

  @Input('aria-labelledby') ariaLabelledby!: string;

  @Input() value: any

  @Input()
  get labelPosition(): DscRadioButtonLabelPosition {
    return this._labelPosition;
  }

  set labelPosition(value: DscRadioButtonLabelPosition) {
    this._labelPosition = value;
  }

  private _labelPosition: DscRadioButtonLabelPosition = 'after';

  get checked(): boolean {
    return this._checked;
  }
  set checked(value: BooleanInput) {
    this._checked = coerceBooleanProperty(value);
  }

  private _checked: boolean = false;

  @Input()
  get required(): boolean {
    return this._required;
  }
  set required(value: BooleanInput) {
    this._required = coerceBooleanProperty(value);
  }

  private _required: boolean = false;

  @Input()
  get size(): DscRadioButtonSize {
    return this._size;
  }

  set size(value: DscRadioButtonSize) {
    this._size = value;
    this.radioButtonLabelSize = `mat-mdc-radio-button--${value}`;
  }

  private _size: DscRadioButtonSize = 'standard';

  protected radioButtonLabelSize = 'mat-mdc-radio-button--standard';

  @Input()
  get variant(): DscRadioButtonVariant {
    return this._variant;
  }

  set variant(value: DscRadioButtonVariant) {
    this._variant = value;
    this.radioButtonVariant = `mat-mdc-radio-button--${value}`;
  }

  private _variant: DscRadioButtonVariant = 'highlight';

  protected radioButtonVariant = 'mat-mdc-radio-button--highlight';

  @Output() readonly change: EventEmitter<any> = new EventEmitter<any>();

  @Output() readonly focus: EventEmitter<void> = new EventEmitter<void>();

  override writeValue(value: any): void {
    this.checked = value && this.value && JSON.stringify(value) === JSON.stringify(this.value);
  }

  select(): void {
    if (!this.disabled) {
      this.checked = true;
      this.onChange(this.value);
      this.onTouched();
    }
  }

  onChanged($event: MatRadioChange): void {
    this.change.emit($event);
  }

  validate(control: AbstractControl<any, any>): ValidationErrors | null {
    return control.value === null || control.value === undefined ? { required: true } : null;
  }
}
